<template>
  <div>
    <SimpleNotes v-if="tokenAdminChecking" title=""
        icon="fas fa-edit" primaryText="Netunion "
        secondaryText=" Editor ..." button="hide" />
    <div v-else class="mt-6 mb-6">
      <ValidationObserver ref="observer">
        <v-form
          class="content"
          autocomplete="off"
        >
          <v-row>
            <v-col cols="3" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="selectionRequired|oneOf: / News, / Blog"
              >
                <v-select
                  v-model="tweet.type"
                  :items="tweetTypes"
                  :error-messages="errors"
                  :disabled="type ? true : false"
                  label=" / Type"
                  outlined
                  hint=""
                  :persistent-hint="tweet.type != '' && !type"
                ></v-select>
              </ValidationProvider>
            </v-col>
            <v-col cols="9" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="contentRequired"
              >
                <v-text-field
                  v-model="tweet.title"
                  :counter="30"
                  :error-messages="errors"
                  label=" / Title"
                  outlined
                ></v-text-field>
              </ValidationProvider>
            </v-col>
            <v-col v-if="tweet.type == ' / News'" cols="3" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="selectionRequired"
              >
                <v-select
                  v-model="tweetNews.tag"
                  :items="tweetNewsTags"
                  :error-messages="errors"
                  :disabled="type ? true : false"
                  label=" / Tag"
                  outlined
                  hint=""
                  :persistent-hint="tweetNews.tag != '' && !type"
                ></v-select>
              </ValidationProvider>
            </v-col>
            <v-col v-if="tweet.type == ' / News'" cols="3" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="selectionRequired"
              >
                <v-select
                  v-model="tweetNews.accent"
                  :items="tweetNewsAccents"
                  :error-messages="errors"
                  label=" / Accent"
                  outlined
                  :color="$Utils.getNewsAccentFromText(tweetNews.accent)"
                ></v-select>
              </ValidationProvider>
            </v-col>
            <v-col v-if="tweet.type == ' / Blog'" cols="6" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="blogTagsInput"
              >
                <v-text-field
                  v-model="tweetBlogInputTags"
                  :error-messages="errors"
                  label=" / Tags"
                  hint=" # "
                  outlined
                ></v-text-field>
              </ValidationProvider>
            </v-col>
            <v-col v-if="tweet.type" cols="6" class="pa-1">
              <v-text-field
                v-model="tweet.location"
                label=" / Location"
                outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" class="pa-1">
              <ValidationProvider
                v-slot="{ errors }"
                name=""
                rules="contentRequired"
              >
                <v-textarea
                  v-model="tweet.content"
                  :error-messages="errors"
                  label=" / Tweet Content"
                  auto-grow
                  hint=" Markdown "
                  rows="10"
                  outlined
                ></v-textarea>
              </ValidationProvider>
            </v-col>
            <v-col cols="12" class="pa-1">
              <v-text-field
                v-model="tweet.link"
                label=" / Link"
                hint=" https://  http:// "
                outlined
              ></v-text-field>
            </v-col>
          </v-row>

          <div class="mt-2 text-end">
            <v-btn
              @click="submitEditor"
              class="mt-2"
              color="primary"
            >{{ (type && id) ? '' : '' }}</v-btn>
            <v-btn
              :disabled="tweet.type == '' || tweet.title == '' ||
                  tweet.content == ''"
              class="mt-2 ml-4"
              @click="previewTweet"
              depressed
            ></v-btn>
            <v-btn
              v-if="type && id"
              class="mt-2 ml-4"
              @click="resetEditor"
              depressed
            ></v-btn>
          </div>
        </v-form>
      </ValidationObserver>
    </div>
    <div class="text-center">
      <v-overlay :value="overlay">
        <v-progress-circular indeterminate size="64"></v-progress-circular>
      </v-overlay>
    </div>
  </div>
</template>

<script>
import SimpleNotes from '@/components/Model/SimpleNotes';

import {required} from 'vee-validate/dist/rules';
import {
  extend,
  ValidationProvider,
  ValidationObserver,
  setInteractionMode,
} from 'vee-validate';

// Does not validate unless validate is called explicitly.
setInteractionMode('eager');

extend('contentRequired', {
  ...required,
  message: ' {_field_} ',
});

extend('selectionRequired', {
  ...required,
  message: ' {_field_} ',
});

extend('oneOf', (value, values) => {
  if (values.indexOf(value) !== -1) {
    return true;
  }

  return '';
});

extend('blogTagsInput', (value) => {
  if (value.indexOf(' ') !== -1) {
    return '';
  }
  return true;
});

export default {
  name: 'Editor',
  components: {
    SimpleNotes,
    ValidationProvider,
    ValidationObserver,
  },
  data: () => ({
    tokenAdminChecking: true,
    tweetTypes: [],
    tweetBlogInputTags: '',
    tweetNewsAccents: [],
    tweetNewsTags: [],
    //  tweet 
    tweet: {
      type: '',
      title: '',
      content: '',
      location: '',
      link: '',
    },
    tweetBlog: {
      tags: [],
    },
    tweetNews: {
      accent: '',
      tag: '',
    },
    //  tweet 
    originalTweet: {
      type: '',
      title: '',
      content: '',
      location: '',
      link: '',
    },
    originalTweetBlog: {
      tags: [],
    },
    originalTweetNews: {
      accent: '',
      tag: '',
    },
    // 
    overlay: false,
  }),
  props: [
    'type',
    'id',
  ],
  methods: {
    previewTweet() {
      this.$refs.observer.validate();
      if (this.$refs.observer.flags.invalid) {
        return;
      }
      switch (this.tweet.type) {
        case ' / News':
          this.$Bus.$emit('setReaderDialog', {
            type: 'news',
            title: this.tweet.title,
            date: new Date().toLocaleDateString(),
            author: localStorage.username,
            content: this.tweet.content,
            location: this.tweet.location,
            link: this.tweet.link,
            accent: this.tweetNews.accent,
            newsTag: this.tweetNews.tag,
          });
          break;
        case ' / Blog':
          this.$Bus.$emit('setReaderDialog', {
            type: 'blog',
            title: this.tweet.title,
            date: new Date().toLocaleDateString(),
            author: localStorage.username,
            content: this.tweet.content,
            location: this.tweet.location,
            link: this.tweet.link,
            blogTags: this.tweetBlog.tags,
          });
          break;
        default:
          //
      }
    },
    submitEditor() {
      this.$refs.observer.validate();
      if (this.$refs.observer.flags.invalid) { // 
        return;
      }

      if (this.type && this.id) { // 
        const confirm = window.confirm('');
        if (!confirm) { // 
          return;
        }

        this.overlay = true; //  overlay 
        if (this.$DevMode) { // 
          console.log('');
          setTimeout(() => {
            this.$Bus.$emit('setSnackbar', {
              text: '',
              type: 'info',
            });
            this.overlay = false;
            this.$router.push({path: '/'});
          }, 2000);
          return;
        }

        if (this.type == 'news') {
          this.axios.post('/editor/modifyNewsTweet', {
            newsId: this.id,
            newsAccent: this.$Utils
                .getNewsAccentFromText(this.tweetNews.accent),
            newsTitle: this.tweet.title,
            newsContent: this.tweet.content,
            newsLocation: this.tweet.location,
            newsLink: this.tweet.link,
          }).then((Response) => {
            // console.log(Response);
            if (Response.data.code == 200) {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'info',
              });
              this.overlay = false;
              this.$router.push({path: '/'});
            } else {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'error',
              });
              this.overlay = false;
            }
          });
        } else if (this.type == 'blog') {
          this.axios.post('/editor/modifyBlogTweet', {
            blogId: this.id,
            blogTags: this.tweetBlog.tags,
            blogTitle: this.tweet.title,
            blogContent: this.tweet.content,
            blogLocation: this.tweet.location,
            blogLink: this.tweet.link,
          }).then((Response) => {
            // console.log(Response);
            if (Response.data.code == 200) {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'info',
              });
              this.overlay = false;
              this.$router.push({path: '/'});
            } else {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'error',
              });
              this.overlay = false;
            }
          });
        }
      } else { // 
        const confirm = window.confirm('');
        if (!confirm) { // 
          return;
        }

        this.overlay = true; //  overlay 
        if (this.$DevMode) { // 
          console.log('');
          setTimeout(() => {
            this.$Bus.$emit('setSnackbar', {
              text: '',
              type: 'success',
            });
            this.overlay = false;
            this.$router.push({path: '/'});
          }, 2000);
          return;
        }

        if (this.tweet.type == ' / News') {
          this.axios.post('/editor/createNewsTweet', {
            newsTag: this.$Utils
                .getNewsTagFromText(this.tweetNews.tag),
            newsAccent: this.$Utils
                .getNewsAccentFromText(this.tweetNews.accent),
            newsTitle: this.tweet.title,
            newsContent: this.tweet.content,
            newsLocation: this.tweet.location,
            newsLink: this.tweet.link,
          }).then((Response) => {
            // console.log(Response);
            if (Response.data.code == 200) {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'success',
              });
              this.overlay = false;
              this.$router.push({path: '/'});
            } else {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'error',
              });
              this.overlay = false;
            }
          });
        } else if (this.tweet.type == ' / Blog') {
          this.axios.post('/editor/createBlogTweet', {
            blogTags: this.tweetBlog.tags,
            blogTitle: this.tweet.title,
            blogContent: this.tweet.content,
            blogLocation: this.tweet.location,
            blogLink: this.tweet.link,
          }).then((Response) => {
            // console.log(Response);
            if (Response.data.code == 200) {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'success',
              });
              this.overlay = false;
              this.$router.push({path: '/'});
            } else {
              this.$Bus.$emit('setSnackbar', {
                text: '',
                type: 'error',
              });
              this.overlay = false;
            }
          });
        } else {
          return;
        }
      }
    },
    resetEditor() { //  tweet 
      const confirm = window.confirm('');
      if (!confirm) { // 
        return;
      }
      // console.log('');

      if (this.$DevMode) {
        this.tweet.title = '';
        this.tweet.content = '';
        this.tweet.location = '';
        this.tweet.link = '';
        this.tweetBlogInputTags = '';
        this.tweetBlog.tags = [];
        this.tweetNews.accent = '';
        return;
      }

      if (this.type == 'news') {
        this.tweet = JSON.parse(sessionStorage.originalTweet);
        this.tweetNews = JSON.parse(sessionStorage.originalTweetNews);
      } else if (this.type == 'blog') {
        this.tweet = JSON.parse(sessionStorage.originalTweet);
        this.tweetBlog = JSON.parse(sessionStorage.originalTweetBlog);

        const blogTagsArray = this.tweetBlog.tags;
        const blogTagsValue = blogTagsArray.join('#');
        this.tweetBlogInputTags = blogTagsValue;
      } else {
        console.log('type error');
        return false;
      }
    },
  },
  created() {
    if (this.type && this.id) {
      if (this.type != 'news' && this.type != 'blog') { // 
        this.$router.push({path: '/'});
      }
    }

    // 
    this.tweetTypes = this.$Common.tweet.typeText;
    this.tweetNewsAccents = this.$Common.tweet.news.accentText;
    this.tweetNewsTags = this.$Common.tweet.news.tagText;

    if (this.type && this.id) { //  type  id 
      if (this.$DevMode) { // 
        switch (this.type) {
          case 'news':
            this.originalTweet.type = ' / News';
            this.originalTweetNews.tag = '';

            sessionStorage.originalTweet =
                JSON.stringify(this.originalTweet);
            sessionStorage.originalTweetNews =
                JSON.stringify(this.originalTweetNews);

            this.tweet = this.originalTweet;
            this.tweetNews = this.originalTweetNews;
            break;
          case 'blog':
            this.originalTweet.type = ' / Blog';

            sessionStorage.originalTweet =
                JSON.stringify(this.originalTweet);

            this.tweet = this.originalTweet;
            break;
          default:
            this.$router.push({path: '/'});
            return;
        }
        setTimeout(() => {
          this.tokenAdminChecking = false;
        }, 1000);
        return;
      }

      this.axios.post('/users/checkTokenAdmin').then((Response) => {
        if (Response.data.code == 200) { //  token 
          // 
          switch (this.type) {
            case 'news':
              this.axios.post('/editor/getOriginalNewsTweet', {
                newsId: this.id,
              }).then((Response) => {
                // console.log(Response.data);
                if (Response.data.code == 200) {
                  const newsItem = Response.data.result;
                  this.originalTweet.type = ' / News';
                  this.originalTweet.title = newsItem.newsTitle;
                  this.originalTweet.content = newsItem.newsContent;
                  this.originalTweet.location = newsItem.newsLocation;
                  this.originalTweet.link = newsItem.newsLink;
                  this.originalTweetNews.tag =
                        this.$Utils.getNewsTagText(newsItem.newsTag);
                  this.originalTweetNews.accent =
                        this.$Utils.getNewsAccentText(newsItem.newsAccent);

                  sessionStorage.originalTweet =
                        JSON.stringify(this.originalTweet);
                  sessionStorage.originalTweetNews =
                        JSON.stringify(this.originalTweetNews);

                  this.tweet = this.originalTweet;
                  this.tweetNews = this.originalTweetNews;
                  this.tokenAdminChecking = false;
                } else { // 
                  this.$router.push({path: '/'});
                }
              });
              break;
            case 'blog':
              this.axios.post('/editor/getOriginalBlogTweet', {
                blogId: this.id,
              }).then((Response) => {
                // console.log(Response.data);
                if (Response.data.code == 200) {
                  const blogItem = Response.data.result;
                  this.originalTweet.type = ' / Blog';
                  this.originalTweet.title = blogItem.blogTitle;
                  this.originalTweet.content = blogItem.blogContent;
                  this.originalTweet.location = blogItem.blogLocation;
                  this.originalTweet.link = blogItem.blogLink;
                  this.originalTweetBlog.tags = blogItem.blogTags;

                  sessionStorage.originalTweet =
                        JSON.stringify(this.originalTweet);
                  sessionStorage.originalTweetBlog =
                        JSON.stringify(this.originalTweetBlog);

                  this.tweet = this.originalTweet;
                  this.tweetBlog = this.originalTweetBlog;

                  const blogTagsArray = this.tweetBlog.tags;
                  const blogTagsValue = blogTagsArray.join('#');
                  this.tweetBlogInputTags = blogTagsValue;

                  this.tokenAdminChecking = false;
                } else { // 
                  this.$router.push({path: '/'});
                }
              });
              break;
            default: // 
              this.$router.push({path: '/'});
              return;
          }
        } else {
          return;
        }
      });
    } else { //  type  id 
      if (this.$DevMode) { // 
        setTimeout(() => {
          this.tokenAdminChecking = false;
        }, 1000);
        return;
      }

      this.axios.post('/users/checkTokenAdmin').then((Response) => {
        if (Response.data.code == 200) { //  token 
          this.tokenAdminChecking = false;
        } else {
          return;
        }
      });

      //  sessionStorage 
      sessionStorage.removeItem('originalTweet');
      sessionStorage.removeItem('originalTweetNews');
      sessionStorage.removeItem('originalTweetBlog');
    }
  },
  watch: {
    tweetBlogInputTags(val) {
      const tags = val.split('#');
      const blogTags = [];
      for (let i = 0; i < tags.length; i++) {
        const tag = tags[i];
        if (tag.length != 0 &&
            blogTags.indexOf(tag) == -1) {
          blogTags.push(tag);
        }
      }
      this.tweetBlog.tags = blogTags;
    },
  },
};
</script>

